import ports from '@/config/ports.js'
import Axios from 'axios';

let origin = location.origin
let vRouter
let API = {}

// Add a request interceptor
Axios.interceptors.request.use( config => {
    // Do something before request is sent
    return config;
}, error => {
    // Do something with request error
    return Promise.reject(error);
})

// Add a response interceptor
Axios.interceptors.response.use(function (response) {
    // Do something with response data
    return response;
}, function (error) {
    // Do something with response error
    return Promise.reject(error);
});

function run(api) {
    return ( data, callback ) => {
        let path = api instanceof Object ? api.path || '/' : api
        let result = new Promise( ( resolve, reject ) => {
            Axios({
                method: api.type || 'POST',
                url: origin + path,
                data: data
            }).then( res => {
                resolve(res)
            }, error => {
                reject(error)
            })
        })
        return result
    }
}

for (const api in ports.list) {
    if (ports.list.hasOwnProperty(api)) {
        API[api] = run(ports.list[api])
    }
}

function init(Vue) {
    Object.defineProperty( Vue.prototype, 'API', { value: API } )
    window.router = vRouter = Vue.router
    console.log(Vue,vRouter);
}

export default {
    install(Vue) {
        init(Vue)
    }
}